<template>
    <div class="header-container">
        <span class="logo"></span>
        <div class="tabs-container">
        	<div v-for="(e, t) in tabs" :key="t" class="tab-item">
        		<router-link :to="e.path">{{e.name}}</router-link>
        	</div>	 
        </div>
        <div class="login-info">
		    <div v-if="logined" class="user-info">
		        <p class="user-name">
		            <span>您好,</span>
		            <span class="mobile">{{userInfo.email || userInfo.mobile}}</span>
		        </p>
		        <el-button type="text" class="quit-btn" @click="quit">
		           退出
		        </el-button>
		    </div>
		    <el-button v-else @click="toLogin">
                登录
            </el-button>
		</div>
    </div>

</template>
<script>
export default {
	name: 'navigator',
	data() {
		return {
            logined: !1,
            userInfo: {},
            tabs: [{
                name: "首页",
                path: "/"
            }, {
                name: "功能介绍",
                path: "/about"
            }, {
                name: "在线申请",
                path: "/apply"
            }, {
                name: "常见问题",
                path: "/question"
            }]
        }
	},
	methods: {
		quit() {

		},
		toLogin() {

		}
	}
}
</script>


