<template>
	<div class="mihoyo-upload-container">
	    <div v-for="(e, index) in imgs" class="upload-img-item" :key="index">
	        <div class="img-display">
	            <i class="el-icon-close" @click="()=>{removeImg(index)}"></i>
	            <img :src="e.url" />
	            <i class="el-icon-plus"></i>
	            <p class="img-tips">{{tipsList ? tipsList[index] : '上传图片'}}</p></div>
	        <input type="file" accept="image/png, image/jpeg" class="upload-handler" @change="(e) => {return initHandler(e, index)}">
	    </div>
	</div>
</template>
<script>
import api from '../../models/api'
export default {
	name: "mihoyo-upload",
	props: ["maxLimit", "imgList", "initLength", "tipsList"],
	data() {
		return {
			temp: {
                url: "",
                tip: "上传图片"
            },
            isInit: true,
            imgs: this.imgList || [{}]
		}
	},
	watch: {
		imgs: {
			handler(t) {
				this.$emit('upload', t.filter((t) => t.url), this.isInit)
			},
			immediate: true,
			deep: true
		}
	},
	mounted() {
		console.log(this.initLength && this.initLength > 1)
		if(this.imgList && 0 !== this.imgList.length) {
			this.maxLimit && this.imgList.length < this.maxLimit && this.imgs.push(this.temp)
		}
		else if(this.initLength && this.initLength > 1) {
			for(let i = 1; i <= this.initLength; i++) {
				this.imgs.push(this.temp)
			}
		} else {
			this.imgs.push(this.temp)
		}
	},
	methods: {
		initHandler(e, index) {
			let file = e.target.files[0];
			if(file) {
				this.isInit = false;
				let data = new FormData;
				data.append('file[]', file);
				api.uploadImg({
					loading: true,
					withCredentials: true,
					headers: {
                        "Content-Type": "multipart/form-data"
                    },
                    data,
                    error: (res)=> {
						const status = res.status;
						const statusText = res.statusText;
						confirm.show({
							title: statusText,
							hideCancel: true,
							submitCallback: ()=> {
								-100 === status && this.$router.push({
									name: 'home'
								})
							}
						})
                    }
				}).then(res => {
					const [file_response] = res.data;
					this.imgs.splice(index, 1, file_response);
					(!this.maxLimit || this.imgs.length < this.maxLimit) && this.imgs.push(this.temp);
					this.handlerImgRepeat(e.target, index)
				})
			}
		}, 
		handlerImgRepeat(e, index) {
			const pNode = e.parentNode;
			pNode.removeChild(e);
			let inputEl = document.createElement('input')
			inputEl.setAttribute("type", "file")
			inputEl.className = "upload-handler"
			inputEl.setAttribute("accept", "image/png, image/jpeg")
			inputEl.onchange = (e) => {
				return  this.initHandler(e, index)
			}
			setTimeout(() => {
				pNode.appendChild(inputEl)
			}, 500)
		},
		removeImg(index) {
			this.isInit = false;
			this.imgs.length <= 1 && this.imgs.splice(index, 1, this.temp);
			this.initLength && this.initLength >= this.imgs.length 
			? this.imgs.splice(index, 1, this.temp) 
			: this.imgs.splice(index, 1);
			(!this.maxLimit || this.imgs.length < this.maxLimit) && this.imgs[this.imgs.length - 1].url && this.imgs.push(this.temp)
		}
	}
}
</script>


