<template>
	
    <div class="apply-progress">
        <progress value="0" max="90" id="progress" class="progress-component"></progress>
        <div class="step-container" >
            <span 
                v-for="(e) in steps" 
                class="step-item" 
                :class="{ 'active':currentStep >= e.index }" 
                :key="e.index"
            >
                <span class="step-icon">{{e.index}}</span>
                <span class="step-desc">{{e.desc}}</span>
            </span>
        </div>
    </div>
	
</template>
<script>
export default{
	name: 'apply-progress',
    props: ["step"],
    watch: {
        step(t) {
            this.getVal(t)
        }
    },
    mounted() {
        this.getVal(this.step)
    },
    methods: {
        getVal(t) {
            const node = document.getElementById("progress");
            switch (t) {
            case 1:
                node.value = 0, this.currentStep = 1;
                break;
            case 2:
                node.value = 30, this.currentStep = 2;
                break;
            case 3:
                node.value = 60, this.currentStep = 3;
                break;
            case 4:
                node.value = 69, this.currentStep = 3;
                break;
            case 5:
                node.value = 77, this.currentStep = 3;
                break;
            case 6:
            case 7:
                node.value = 90, this.currentStep = 4
            }
        }
    },
    data() {
        return {
            currentStep: 1,
            steps: [{
                index: 1,
                desc: "填写被监护人信息"
            }, {
                index: 2,
                desc: "填写监护人信息"
            }, {
                index: 3,
                desc: "确认信息"
            }, {
                index: 4,
                desc: "提交申请"
            }]
        }
    }
}
</script>


