import {get, post} from '@/utils/http';

export default {
  /**
   * 获取用户信息
   * @param options
   * @returns {*}
   */
  getGameInfo: function(options = {}) {
    return get('/jiazhang/api/getGameInfo', options);
  },
  uploadImg: function(options = {}) {
    return post('/jiazhang/api/uploadImg', options);
  },
}



