<template>
	<div class="apply-container">
		<div class="top-container">
			<navigator />
        	<info />
		</div>
		<div class="content-container">
			<div class="step-container">
				<apply-progress :step="activeStep"/>
			</div>
			<div v-for="(e) in formLabelList" class="form-container" :key="e.index">
				<div v-if="activeStep === e.index">
					<p class="step-title">填写被监护人信息<span class="tips">(下列信息皆为必填项，请认真填写)</span></p>
					<div class="navigator-history-container">
	                  如之前有过申请记录，可点击此处查看<span class="navigator-history">在线申请记录</span>
	              	</div>
					<el-form v-if="1 === activeStep || 2 === activeStep" :model="submitForm" label-width="200px" class="step-form-item">
						<el-form-item 
							v-for="n in e.labelList"
							:label="n.text"
							:key="n.text"
							:class="[{'upload-form-container': 'upload' === n.type}]"
						>
							<div :class="['form-item-content-area', {'error': n.showError}]">
								<el-input 
                                    v-if="'input' === n.type" 
                                    v-model="submitForm[e.key][n.key]" 
                                    @blur="(e)=> {
                                        handleValidate(e.target.value, n)
                                    }"
                                ></el-input>
								<template v-if="'select' === n.type">
									<el-select 
										v-if="'game' === n.key"  
										v-model="selectedGame" 
										filterable 
										placeholder="请选择"
										@change="(val) => {changeGame(val, n)}"
									>
										<el-option 
											v-for="e in gameList" 
											:key="e.value" 
											:label="e.label"
											:value="e.value"
										></el-option>
									</el-select>
									<el-select 
										v-else-if="'server' === n.key"  
										v-model="submitForm[e.key][n.key]" 
										filterable 
										placeholder="请选择"
                                        @change="(val) => {handleValidate(val, n)}"
									>
										<el-option 
											v-for="e in serverList" 
											:key="e.value" 
											:label="e.label"
											:value="e.value"
										></el-option>
									</el-select>
									<el-select 
										v-else
										v-model="submitForm[e.key][n.key]" 
										filterable 
										placeholder="请选择"
										@change="(val) => {handleValidate(val, n)}"
									>
										<el-option 
											v-for="e in n.option" 
											:key="e.value" 
											:label="e.label"
											:value="e.value"
										></el-option>
									</el-select>
								</template>
                                <upload 
                                    v-if="'upload' === n.type"
                                    :tipsList="n.tipsList"
                                    :maxLimit="n.limit"
                                    :initLength="n.showImg"
                                    :imgList="computeImgList(submitForm[e.key][n.key])"
                                    @upload="(imgs, isInit) =>{ return onUpload(imgs, isInit, n)}"
                                />
                                <p v-if="n.required && n.showError" class="error-info">{{n.errorMsg}}</p>
							</div>
                            <div v-if="n.explanation"  class="step-form-item-explanation"> {{n.explanation}}</div>
						</el-form-item>
						<el-form-item class="btn-group">
                            <el-button v-if="activeStep > 1"  type="primary" @click="(e) => {activeStep -= 1}">下一步</el-button>
                            <el-button  type="primary" @click="checkCurrentStep">下一步</el-button>
                        </el-form-item>
					</el-form>
                    
                    
    				
				</div>
			</div>
            
		</div>
		
	</div>
</template>
<script>
import Navigator from '@/components/Navigator/Navigator'
import Info from '@/components/Info/Info'
import Upload from '@/components/Upload/Upload'
import ApplyProgress from '@/components/ApplyProgress/ApplyProgress'
import {nationsList, sexList, gameList, serverList, relationList} from '@configPath/otherConfigs/customList.config'
import api from '../../models/api'
export default {
	name: "apply",
	components: {
		Navigator,
		Info,
		ApplyProgress,
        Upload,
	},
	data() {
		return {
			gameMap: {},
			activeStep: 1,
			selectedGame:'',
			submitForm: {
                pupil: {},
                guardian: {},
                refund: {}
            },
			formLabelList: [{
                index: 1,
                key: "pupil",
                title: "填写被监护人信息",
                labelList: [{
                    text: "被监护人姓名",
                    type: "input",
                    key: "name",
                    required: !0,
                    reg: /^[\u4E00-\u9FA5]{1,10}$/,
                    error: "请正确输入被监护人姓名，且不超过10个字"
                }, {
                    text: "被监护人性别",
                    type: "select",
                    option: sexList,
                    key: "sex",
                    required: !0
                }, {
                    text: "被监护人民族",
                    type: "select",
                    option: nationsList,
                    key: "nation",
                    required: !0
                }, {
                    text: "被监护人身份证号",
                    type: "input",
                    key: "id_num",
                    required: !0,
                    error: "请正确输入被监护人身份证号"
                }, {
                    text: "被监护人联系电话",
                    type: "input",
                    key: "phone",
                    required: !0,
                    reg: /^\S{1,15}$/,
                    error: "请正确输入被监护人联系电话，且不超过15个字"
                }, {
                    text: "被监护人居住地址",
                    type: "input",
                    key: "address",
                    required: !0,
                    reg: /^\S{1}(\s|\S){0,49}$/,
                    error: "请正确输入被监护人居住地址，且不超过50个字"
                }, {
                    text: "游戏名称",
                    type: "select",
                    option: [],
                    key: "game",
                    required: !0
                }, {
                    text: "游戏UID",
                    type: "input",
                    key: "game_id",
                    required: !0,
                    reg: /^\S{1,15}$/,
                    error: "请正确输入游戏UID"
                }, {
                    text: "游戏绑定的身份证号",
                    type: "input",
                    key: "bind_id_num",
                    required: !0,
                    error: "请正确输入游戏绑定的身份证号"
                }, {
                    text: "游戏等级",
                    type: "input",
                    key: "level",
                    required: !0,
                    reg: /^\S{1,15}$/,
                    error: "请正确输入游戏等级"
                }, {
                    text: "服务器",
                    type: "select",
                    option: [],
                    key: "server",
                    required: !0
                }, {
                    text: "绑定的手机号或邮箱",
                    type: "input",
                    key: "bind_account",
                    required: !0,
                    reg: /^\S{1,50}$/,
                    error: "请正确输入游戏绑定的手机号或邮箱"
                }, {
                    text: "被监护人身份证扫描件",
                    type: "upload",
                    limit: 2,
                    showImg: 2,
                    hasTips: !0,
                    explanation: "(如孩子没有身份证，可不上传)",
                    tipsList: ["上传身份证正面", "上传身份证反面"],
                    tips: "1. 需提供身份证正反面<br/> 2. 须在身份证有效期内<br/>",
                    key: "id_cards_url",
                    belong: "pupil"
                }, {
                    text: "被监护人户口簿扫描件",
                    type: "upload",
                    limit: 2,
                    showImg: 1,
                    hasTips: !0,
                    tipsList: ["上传户口簿扫描件", "上传户口簿扫描件"],
                    tips: "1. 须为被监护人户口本页<br/> 2. 如监护人与被监护人户籍不同，须再提供双方法律关系证明<br/> （如：公安机关监护证明、出生证等）",
                    key: "residence_url",
                    required: !0,
                    belong: "pupil"
                }, {
                    text: "游戏充值订单截图",
                    type: "upload",
                    limit: 4,
                    showImg: 3,
                    hasTips: !0,
                    tips: "充值订单须包含充值订单号、商户信息等",
                    explanation: "(如无特殊情况请务必上传)",
                    key: "charge_url",
                    belong: "pupil"
                }]
            }, {
                index: 2,
                key: "guardian",
                title: "填写监护人信息",
                labelList: [{
                    text: "监护关系",
                    type: "select",
                    option: relationList,
                    key: "relation",
                    required: !0
                }, {
                    text: "监护人姓名",
                    type: "input",
                    key: "name",
                    required: !0,
                    reg: /^[\u4E00-\u9FA5]{1,10}$/,
                    error: "请正确输入监护人姓名，且不超过10字"
                }, {
                    text: "监护人性别",
                    type: "select",
                    option: sexList,
                    key: "sex",
                    required: !0
                }, {
                    text: "监护人民族",
                    type: "select",
                    option: nationsList,
                    key: "nation",
                    required: !0
                }, {
                    text: "监护人身份证号",
                    type: "input",
                    key: "id_num",
                    required: !0,
                    error: "请正确输入监护人身份证号"
                }, {
                    text: "监护人联系电话",
                    type: "input",
                    key: "phone",
                    required: !0,
                    reg: /^\S{1,15}$/,
                    error: "请正确输入监护人联系电话，且不超过15个字"
                }, {
                    text: "监护人居住地址",
                    type: "input",
                    key: "address",
                    required: !0,
                    reg: /^\S{1}(\s|\S){0,49}$/,
                    error: "请正确输入监护人居住地址，且不超过50个字"
                }, {
                    text: "监护人身份证扫描件",
                    type: "upload",
                    key: "id_cards_url",
                    required: !0,
                    belong: "guardian",
                    tipsList: ["上传身份证正面", "上传身份证反面"],
                    hasTips: !0,
                    tips: "1. 需提供身份证正反面<br/> 2. 须在身份证有效期内",
                    limit: 2,
                    showImg: 2
                }, {
                    text: "监护人户口本扫描件",
                    type: "upload",
                    key: "residence_url",
                    required: !0,
                    belong: "guardian",
                    tipsList: ["上传户口簿扫描件", "上传户口簿扫描件"],
                    hasTips: !0,
                    tips: "1. 须为监护人户口本页",
                    limit: 2,
                    showImg: 1
                }]
            }]

		}
	},
	computed:{
		gameList() {
			const list = []
			if(0 === Object.keys(this.gameMap).length) return []
			else {
				Object.keys(this.gameMap).forEach((c) => {
					list.push({
						label: this.gameMap[c].name,
						value: c
					})
				})
				return list || []
			}

		},
		serverList() {

			const { pupil: { game } } = this.submitForm
			const list = []
			if(!game || 0 === Object.keys(this.gameMap).length) return []
			const server = this.gameMap[game].server
			Object.keys(server).forEach((c) => {
				list.push({
					label: server[c],
					value: c
				})
			})

			return list || []
		}
	},
	created() {
		const {params} = this.$route;
        0 !== Object.keys(params).length && (this.submitForm = params, this.selectedGame = params.pupil.game)
        this.getGameInfo()
	},
	methods: {
		getGameInfo() {
			api.getGameInfo().then(({data})=> {
				this.gameMap = data
			})
		},
		changeGame(val, currObj) {
            val !== this.submitForm.pupil.gameMap && this.$set(this.submitForm.pupil, "server", "")
            
            this.$set(this.submitForm.pupil, "game", val)
            this.handleValidate(val, currObj)
		},
        onUpload(imgs, isInit, currObj){
            if(imgs && !isInit) {
                const imgUrls = imgs.map((e) => e.url)
                this.$set(this.submitForm[currObj.belong], [currObj.key], imgUrls)
                this.handleValidate(imgUrls, currObj)
            }
        },
        computeImgList(t) {
            if(t && t.length > 0){
                const list =[]
                t.map((t) => list.push({
                    url: t
                }))
                return list
            }
            return []
        },
        checkCurrentStep() {
            if(1 === this.activeStep  ||2 == this.activeStep) {
                if(this.validateForm()) {
                    if(1 === this.activeStep) {
                        this.submitForm.guardian.address || this.$set(this.submitForm.guardian, "address", this.submitForm.pupil.address)
                    }
                    this.activeStep += 1
                } else {
                    /*confirm.show({
                        title: '',
                        hideCancel: true
                    })*/
                }
            } 
            else {
                this.activeStep += 1
            }
        },
        validateForm() {

            const form = this.formLabelList.find((e) => e.index === this.activeStep);
            const keys = form.labelList.map(e=> e.key)
            const index = form.key;
            const submitForm = this.submitForm[index];
            const data = [];
            form.labelList.forEach((c, i) => {
                data[c.key] = Object.assign({}, c, {
                    index: i
                })
            })
            let err = true
            for (let i = 0; i < keys.length; i++) {
                const key = keys[i];
                const index = data[key].index;
                const currObj= this.formLabelList[this.activeStep - 1].labelList[index];
                const val = submitForm[key];
                this.handleValidate(val, currObj) || (err = false)
            }
            // return err
            return true


        },
        handleValidate(value, currObj) {
            const {
                text,
                type,
                key,
                reg,
                required,
                error,
                showImg,
            } = currObj;
            let flag = true
            if(required) {
                if("input" === type || "select" === type) {
                    if(value) {
                        if(reg && !reg.test(value)) {
                            this.$set(currObj, "showError", true)
                            this.$set(currObj, "errorMsg", error)
                            flag = false
                        } else if(-1 !== ["id_num", "bind_id_num"].indexOf(key)) {
                            this.validIdentity(value) 
                            ? this.$set(currObj, "showError", false)
                            : (this.$set(currObj, "showError", true), this.$set(currObj, "errorMsg", error), flag = false)
                        } else {
                            this.$set(currObj, "showError", false)
                        }
                    } else {
                        this.$set(currObj, "showError", true)
                        this.$set(currObj, "errorMsg", text + "不能为空")
                        flag = false
                    }
                } else if("upload" === type) {
                    if(value && 0 !== value.length) {
                        if(value.length < showImg ) {
                            this.$set(currObj, "showError", true)
                            this.$set(currObj, "errorMsg", text + "必须为" + showImg + "张")
                            flag = false
                        } else{
                            this.$set(currObj, "showError", false)
                        }
                    } else{
                        this.$set(currObj, "showError", true)
                        this.$set(currObj, "errorMsg", text + "不能为空")
                        flag = false
                    }
                }
            } else {
                this.$set(currObj, "showError", false)
            }
            return flag
        }, 
        validIdentity(t) {
            if (!/^[0-9]{17}[0-9xX]{1}$/.test(t)) return false;
            const factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
            const code = String(t).split("");
            let sum = 0
            for(let i=0; i< 17; i++) {
                sum += factor[i] * code[i]
            }
            return 2 === (sum % 11 )
            ? "x" === code[17] || "X" === code[17] 
            : ["1", "0", "X", "9", "8", "7", "6", "5", "4", "3", "2"][sum % 11] === code[17]
        }
	}
}
</script>


